import { _decorator } from "cc";
import { Actor } from "../Actor";
import { HeroProperty } from "./HeroProperty";
import { GUIManager } from "drongo-gui";
import wuxian_GameOver from "../../../fgui/wuxian/wuxian_GameOver";

const { ccclass, property, requireComponent } = _decorator;

/** */
@ccclass('Hero')
export class Hero extends Actor {
    autoAtk = false;
    actorProperty: HeroProperty = new HeroProperty();


    onDie() {
        super.onDie();
        GUIManager.open(wuxian_GameOver);
    }
}