import { math, _decorator } from 'cc';

/**
 * Actor 
 */
export class ActorProperty {

    /**
     * 
     */
    maxHp : number = 100;

    /**
     * 
     */
    hp: number = this.maxHp;    

    /**
     * 
    */
    attack: number = 10;

    /**
     * 
     */
    level: number = 1;

    //#region 
    /**
     * 
     */
    chaseRate: number = 0;

    /**
     * 
     */
    penetration: number = 0;

    /**
     * 
     */
    projectileCount: number = 1;

    //#region 

    /**
     * 
     */
    exp: number = 0;

    /**
     * 
     */
    maxExp: number = 20;

    /**
     * 
     */
    get hpPercent():number{
        return math.clamp01(this.hp/this.maxHp);
    }
}

