import { Camera, Component, director, IVec3Like, Node } from "cc";
import { Assets, baseAtk, EventType } from "./Enum/GameEnum";
import { BundleMgrImpl } from "drongo-cc";
import { EffectCtrl } from "./Ctrl/EffectCtrl";


export default class Global {
    //
    public static timeScale = 1;
    public static LoadingRate = 0;

    public static cam: Camera = null;
    public static layer: Node[] = [];
    /**0,123,4 */
    public static stage: Node[] = [];
    public static GamePause = true;

    /** */
    public static DrawLevel = 4;
    public static firstLoad = true;
    public static BoxVideo = false;
    public static Speed = 1;

    public static homeload = false;
    public static gameload = false;
    public static subload = false;
    public static isLoading = false;
    public static atkBuff = 1;
    public static comboPoint = 0;
    public static itemDrop = 0;
    public static chapter = 1;
    public static realLevel = 1;
    public static camRadius = 7.5;

    public static playerAtk: baseAtk = { dmg: 50, crit: 0.1 };


    public static currLevel = 0


    static Debug = false;

    public static isGame = true;



    static isFreeze = false
    static freezescale = 0
    public static freeze(scale: number, time: number) {
        if (this.isFreeze) return
        this.isFreeze = true
        this.freezescale = scale
        Global.timeScale = scale
        setTimeout(() => {
            this.isFreeze = false
            Global.timeScale = 1
        }, time * 1000);
    }


    public static tweenCam(num: number, time: number, speed = 5) {
        director.emit(EventType.TweenCam, num, time, speed)
    }
    /**
    * @name: 
    * @msg: 
    * @param {Prefab} prefab 
    * @param {true} play 
    * @param {Vec3} pos 
    * @param {*} time 
    * @return {*}
    */
    public static async playSkill(prefab: string, play: boolean = true, pos?: IVec3Like, time?, eulerY?, parent?) {

        //    const node = this.getNode(prefab, parent ? parent : Global.stage[3], pos)
        const node = await skillBundleMgr.getPrefabNode(prefab);
        node.parent = parent ? parent : Global.stage[3];
        pos && node.setPosition(pos.x, pos.y, pos.z);

        if (play) {
            node.getComponent(EffectCtrl).play()
        }

        if (time) {
            const skillCtrl = node.getComponent(EffectCtrl);
            skillCtrl.scheduleOnce(() => {
                if (play) {
                    node.getComponent(EffectCtrl).stop()
                }

                skillBundleMgr.recoverNode(node);
            }, time);

        }
        if (eulerY) {
            node.setRotationFromEuler(0, eulerY);
        }
        return node
    }
}

// 
export const skillBundleMgr = new BundleMgrImpl('SkillBundle', {
    assetsPathConf: Assets, preLoadDir: ['Json']
}, ['game']);

// director.tick 
const originalTick = director.tick;
director.tick = (dt: number) => {
    dt *= Global.timeScale;
    originalTick.call(director, dt);
}

self['Global'] = Global;