import { _decorator, Component, Node, Prefab, Pool, director, instantiate } from 'cc';
import { Events } from '../events/Events';
import { Projectile } from './Projectile';
import { Level } from '../level/Level';
import { GameBundleMgr } from '../resource/ResourceDefine';
const { ccclass, property } = _decorator;

/**
 * 
 */
@ccclass('ProjectileEmitter')
export class ProjectileEmitter extends Component {

    /**
     * 
     */
    @property
    projectile: string = "Arrow";

    start() {
        // const poolCount = 5;

        // this.prefabPool = new Pool(() => {
        //     return instantiate(this.projectile!);
        // }, poolCount, (node: Node) => {
        //     node.removeFromParent();
        // });
    }

    onDestroy() {
    }

    async create() {
        const node = await GameBundleMgr.getPrefabNode(this.projectile, GameBundleMgr.key.Skill);
        if (node.parent == null)
            Level.instance.skillLayer.addChild(node);
        node.active = true;
        node.once(Events.onProjectileDead, this.onProjectileDead, this);
        const projectile = node.getComponent(Projectile);
        return projectile;
    }

    onProjectileDead(projectile: Projectile) {
        projectile.node.active = false;
        GameBundleMgr.recoverNode(projectile.node);
    }
}

