import { _decorator } from 'cc';
const { ccclass, property } = _decorator;
/**
 * 
 */
@ccclass('ProjectileProperty')
export class ProjectileProperty {

    /**
     * 
     */
    @property
    penetration: number = 1;

    /**
     * 
     */
    @property
    liftTime: number = 3.0;

    /**
     * 
     */
    @property
    chase: boolean = false;
}

