import { _decorator, Node, Prefab, Vec3, Pool, instantiate, director, ParticleSystem, resources } from 'cc';
import { DynamicResourceDefine, GameBundleMgr } from '../resource/ResourceDefine';
import { Pools } from '../util/Pools';
import { Level } from '../level/Level';
const { ccclass, property } = _decorator;

/**
 * 
 */
@ccclass('EffectManager')
export class EffectManager {

    static _instance: EffectManager;
    static get instance() {
        if (this._instance == null) {
            this._instance = new EffectManager();
        }
        return this._instance;
    }

    pools: Pools<string, Node> = new Pools();

    destory() {
        EffectManager._instance = null;
    }

    async play(key: string, worldPosition: Vec3) {
        if (!key) return;
        const node = await GameBundleMgr.getPrefabNode(key, GameBundleMgr.key.Effect);
        this.playEffect(node, worldPosition);
    }

    private playEffect(node: Node, worldPosition: Vec3) {
        Level.instance.effectLayer.addChild(node);
        node.worldPosition = worldPosition;
        node.active = true;

        const ps = node.getComponent(ParticleSystem);
        ps.scheduleOnce(() => {
            node.active = false;
            GameBundleMgr.recoverNode(node);
        }, ps.duration);
        ps.play();
    }
}

