import { Component, SkyboxInfo, _decorator, director } from 'cc';
import { GRoot } from 'drongo-fgui';
import { AllBinder, GUIManager } from 'drongo-gui';
import wuxian_GameUI from './fgui/wuxian/wuxian_GameUI';
const { ccclass, property } = _decorator;
const skip = 1;

@ccclass
export default class GameView extends Component {
    private skyBox: SkyboxInfo;
    private _dt = 0;

    onLoad() {
        AllBinder.bindAll();
        //
        const groot = GRoot.create();
    }

    protected start(): void {
        GUIManager.open(wuxian_GameUI);
        this.skyBox = director.getScene().globals.skybox;
    }

    update(dt) {
        this._dt++
        if (this._dt > skip) {
            this._dt = 0;
            this.skyBox.rotationAngle -= dt * 1;
        }
    }
}