import { Component, Font, LabelComponent, Node, NodePool, Prefab, SpriteComponent, UIOpacity, _decorator } from "cc";

import { skillBundleMgr } from "../Global";
import { DmgAnimCtrl } from "./DmgAnimCtrl";

const { ccclass, property } = _decorator;

@ccclass("BloodBarCtrl")
export class BloodBarCtrl extends Component {
    @property({ type: Node })
    nodeNick: Node = null!;
    @property({ type: SpriteComponent })
    hpProgress: SpriteComponent = null!;
    @property({ type: SpriteComponent })
    blankhp: SpriteComponent = null!;
    @property({ type: Node })
    BloodbarBG: Node = null!;

    @property({ type: Node })
    DmgPop: Node = null!
    @property({ type: Font })
    DmgFont: Font[] = [];
    @property
    Player = false;
    @property({ type: Prefab })
    dmgpfb: Prefab = null!;

    private percent = 1;
    private hp: number;
    private isDied = false;

    initBloodBar(hp: number, show = true) {
        // this.DmgPop.setPosition(18,)
        // this.BloodbarBG.setPosition(18,)
        this.percent = this.hpProgress.fillRange = this.blankhp.fillRange = 1;
        this.hp = hp;
        this.nodeNick.getComponent(LabelComponent)!.string = `${hp}`;
        this.node.getComponent(UIOpacity).opacity = show ? 255 : 0;

        this.isDied = false;

        // this.schedule(()=>{
        //     this.onDmgBlood(50,10,true)
        // },0.2,100)
    }




    recycleSelf() {
        this.isDied = true;
        this.unscheduleAllCallbacks();
        for (let i = this.DmgPop.children.length - 1; i >= 0; i--) {
            skillBundleMgr.recoverNode(this.DmgPop.children[0])
        }
        this.hpProgress.fillRange = 1
        this.blankhp.fillRange = 1
        skillBundleMgr.recoverNode(this.node);

    }

    async getDmgpopNode() {
        const dmgpop = await skillBundleMgr.getPrefabNode(this.dmgpfb.name);
        dmgpop.parent = this.DmgPop;
        dmgpop.active = true;

        return dmgpop;
    }

    async onMiss() {
        const dmgpop = await this.getDmgpopNode();
        dmgpop.getComponent(DmgAnimCtrl).init("miss", this.DmgFont[3]);
    }

    onBlankReduce() {
        if (this.isDied) return
        this.blankhp.fillRange = this.percent
        if (this.percent == 0) {
            this.isDied = true;
            this.unscheduleAllCallbacks();
        }
    }


    /**
     * 
     * @param percent 
     */
    async onDmgBlood(hp: number, dmg: number, crit: boolean) {
        // if (type == 1) {
        //     dmg = "+" + dmg
        //     dmgpop.getComponent(DmgAnimCtrl).init(dmg, this.DmgFont[type])
        //     return
        // }
        if (this.isDied) return;
        const type = crit ? 1 : 0;
        const dmgpop = await this.getDmgpopNode();
        dmgpop.getComponent(DmgAnimCtrl).init(dmg, this.DmgFont[type])

        let percent = hp / this.hp;
        if (percent < 0) {
            percent = 0
        }
        // this.unschedule(this.onBlankReduce)
        this.hpProgress.fillRange = this.percent = percent;
        this.scheduleOnce(this.onBlankReduce, 0.15);
    }
}