import { _decorator, Component, Collider, ITriggerEvent } from 'cc';
import { RoleCtrl } from '../CombatCtrl/RoleCtrl';
import { baseAtk, bonusAtk, PHY_GROUP, PHY_MASK } from '../Enum/GameEnum';
import { EffectCtrl } from './EffectCtrl';
const { ccclass, property } = _decorator;

@ccclass('BulletCtrl')
export class BulletCtrl extends Component {
    @property(Collider)
    collider: Collider = null

    private _atk: baseAtk;
    private _bonus: bonusAtk;


    public init(atk: baseAtk, bonus: bonusAtk) {
        this._atk = atk;
        this._bonus = bonus;
        this.collider.on('onTriggerEnter', this.onTriggerEnter, this);
    }

    onDisable() {
        this._atk = null;
        this._bonus = null;
        this.collider.off('onTriggerEnter', this.onTriggerEnter, this);
    }

    private onTriggerEnter(event: ITriggerEvent) {
        const role = event.otherCollider.getComponent(RoleCtrl);
        role && role.beHit(this._atk, this._bonus);
    }
}


