
import { Component, Node, Vec3, _decorator, director } from 'cc';
import { Key } from 'drongo-cc';
import { CharacterType } from '../../Types/CharacterType';
import { BloodBarCtrl } from '../Ctrl/BloodBarCtrl';
import { CharactorType } from '../Enum/GameEnum';
import Global, { skillBundleMgr } from '../Global';

const { ccclass, property } = _decorator;



@ccclass('StatusCtrl')
export class StatusCtrl extends Component {
    @property
    height = 3;

    @property({ displayOrder: 0, tooltip: 'json' })
    id = 0;
    @property({ type: CharactorType, displayOrder: 1, tooltip: '' })
    charactor = CharactorType.None;

    isDie: boolean = true;

    //
    currAttribue: Map<string, number> = new Map<string, number>();

    bloodbarpos = new Vec3();

    bloodBarNode: Node = null!;

    private initted = false;

    Dash = false
    pulse = false
    hurt = false
    _atk = false
    _skill = false
    /** */
    webbed = false
    /** */
    Shield = false
    //
    freeze = false





    onDisable() {
        this.initted = false;
        if (this.bloodBarNode) {
            this.bloodBarNode.getComponent(BloodBarCtrl).recycleSelf();
        }
        this.currAttribue.clear()
        this.node.setRotationFromEuler(0, 0, 0);
        this.unschedule(this.regHP)
    }



    async initStatus() {
        // this.returnNormal()
        this._atk = false;
        const json = await skillBundleMgr.getAsset("character", skillBundleMgr.key.Json)
        const config = json.json[this.id] as CharacterType;
        const key = Key<CharacterType>();
        this.currAttribue.set(key.id, config.id)
        this.currAttribue.set(key.hp, config.hp);
        this.currAttribue.set(key.def, config.def)
        this.currAttribue.set(key.atk, config.atk)
        this.currAttribue.set(key.crit, config.crit)
        this.currAttribue.set(key.miss, config.miss)

        if (this.charactor == CharactorType.Player) {
            Global.playerAtk.node = this.node;
            Global.playerAtk.dmg = config.atk;
            Global.playerAtk.crit = config.crit;

        }


    }



    regHP() {
        const reg = this.currAttribue.get("hpreg")
        if (reg <= 0) return
        this.currAttribue.set('hp', this.currAttribue.get('hp') + reg)
        if (this.currAttribue.get('hp') > this.currAttribue.get('hptotal')) this.currAttribue.set('hp', this.currAttribue.get('hptotal'))
        this.updateBloodBar(reg, 1)
        if (this.charactor === CharactorType.Player) {
            director.emit("playerhp", this.currAttribue.get('hp'), this.currAttribue.get('hptotal'), true)
        }
    }

    lsHp(reg) {
        if (reg <= 0) return
        reg = Math.floor(reg)
        this.currAttribue.set('hp', this.currAttribue.get('hp') + reg)
        if (this.currAttribue.get('hp') > this.currAttribue.get('hptotal')) this.currAttribue.set('hp', this.currAttribue.get('hptotal'))
        this.updateBloodBar(reg, 1)
        if (this.charactor === CharactorType.Player) {
            director.emit("playerhp", this.currAttribue.get('hp'), this.currAttribue.get('hptotal'), true)
        }
    }

    lateUpdate(dt: number) {
        // if (Global.GamePause) return
        if (this.initted) {
            this.bloodbarpos.set(this.node.position);
            this.bloodbarpos.y += 2;
            Global.cam.convertToUINode(this.bloodbarpos, Global.layer[3], this.bloodbarpos);
            this.bloodBarNode.setPosition(this.bloodbarpos)
        }
    }

    /**
    * 
    */
    async initBloodBar(show = true) {

        this.bloodBarNode = await skillBundleMgr.getPrefabNode("BloodBar");
        this.bloodBarNode.parent = Global.layer[3];
        const hp = this.currAttribue.get('hptotal');
        this.bloodBarNode.getComponent(BloodBarCtrl)?.initBloodBar(hp, show)
        this.initted = true;

        // if (this.charactor === CharactorType.Player) {
        //     director.emit("playerhp", this.currAttribue.get('hp'), this.currAttribue.get('hptotal'))
        // }

    }

    closeShield() {
        this.Shield = false
    }


    updateBloodBar(dmg = 0, crit) {
        this.bloodBarNode.getComponent(BloodBarCtrl)!.onDmgBlood(this.currAttribue.get('hp'), dmg, crit)
    }





}
