import { BoxCollider, Component, Node, Rect, RigidBody, Size, Vec3, _decorator, director, macro, math, randomRange, v2, v3 } from 'cc';
import { GRoot } from 'drongo-fgui';
import { GUIManager } from 'drongo-gui';
import wuxian_GameUI from '../../fgui/wuxian/wuxian_GameUI';
import { Actor } from '../actor/Actor';
import { EffectManager } from '../effect/EffectManager';
import { ActorManager } from './ActorManager';
const { ccclass, property } = _decorator;

/**
 * 
 */
@ccclass('Level')
export class Level extends Component {

    private static _instance: Level;
    static get instance() {
        return this._instance;
    }

    /**
     * 
     */
    @property(BoxCollider)
    spawnCollider: BoxCollider | null = null;

    @property(Node)
    enemyLayer: Node = null;
    @property(Node)
    effectLayer: Node = null;
    @property(Node)
    skillLayer: Node = null;

    /** 
     *  
    */
    private spawnRect: Rect = new Rect()

    /**
     * 
     */
    spawnPos: Vec3 = v3()

    /**
     * 
     */
    private spawnHp: number = 10;

    /**
     * 
     */
    private maxAlive: number = 100;

    async start() {
        Level._instance = this;

        const wp = this.spawnCollider.node.worldPosition;
        const size = this.spawnCollider.size;
        this.spawnRect.size = new Size(size.x, size.z);
        this.spawnRect.center = v2(wp.x, wp.z);

        await ActorManager.instance.init();
        GUIManager.open(wuxian_GameUI);
        GRoot.inst.closeModalWait();
        this.startSpawnTimer();
    }

    startSpawnTimer() {
        this.schedule(() => {
            this.randomSpawn()
        }, 1.0, 15, 3)

        this.schedule(() => {
            this.randomSpawn()
        }, 1.0, macro.REPEAT_FOREVER, 10)

        this.schedule(() => {
            this.spawnHp *= 1.2;
        }, 20, macro.REPEAT_FOREVER);
    }

    onDestroy() {
        this.unscheduleAllCallbacks();

        Level._instance = null;
        GUIManager.closeAll();
        ActorManager.instance.clear();
        EffectManager.instance.destory();
    }

    private randomSpawn() {
        if (ActorManager.instance.enemies.length >= this.maxAlive) {
            return;
        }

        if (ActorManager.instance.playerActor.dead) {
            return;
        }

        // this.spawnPos.x = math.randomRange(this.spawnRect.xMin, this.spawnRect.xMax);
        // this.spawnPos.z = math.randomRange(this.spawnRect.yMin, this.spawnRect.yMax);
        const actor = ActorManager.instance.playerActor;
        const pos = actor.node.worldPosition;
        this.spawnPos.x = math.randomRange(pos.x-5, pos.x+5);
        this.spawnPos.z = math.randomRange(pos.z-5, pos.z+5);
        this.doSpawn(this.spawnPos)
    }

    private async doSpawn(spawnPoint: Vec3) {
        const enemy = await ActorManager.instance.createRandomEnemy();
        enemy.setPosition(spawnPoint);
        this.enemyLayer.addChild(enemy);

        const rand = randomRange(1.0, 2.0);
        const actor = enemy.getComponent(Actor);
        actor.actorProperty.hp = this.spawnHp;
        actor.actorProperty.maxHp = this.spawnHp;
        actor.respawn();
        const rigid = enemy.getComponent(RigidBody)!;
        // rigid.mass = rand;
        // enemy.scale = v3(rigid.mass, rigid.mass, rigid.mass);
    }
}
