import { Component, SkyboxInfo, _decorator, director } from 'cc';
import { DEBUG } from 'cc/env';
import * as drongoCC from 'drongo-cc';
import { global_config } from 'drongo-cc';
import * as fgui from "drongo-fgui";
import * as drongoGui from 'drongo-gui';
import { AllBinder, GUIManager } from 'drongo-gui';
import wuxian_StartGame from './fgui/wuxian/wuxian_StartGame';
import wuxian_WindowModalWaiting from './fgui/wuxian/wuxian_WindowModalWaiting';
const { ccclass, property } = _decorator;
//@ts-ignore
global_config.log.output_position_b = true;
const skip = 1;
@ccclass
export default class StartView extends Component {
    private skyBox: SkyboxInfo;
    private _dt = 0;

    async onLoad() {
        //
        const groot = fgui.GRoot.create();
        // director.addPersistRootNode(groot.node);
        AllBinder.bindAll();
        // fgui.UIConfig.buttonSound = "ui://MainMenu/click";
        // fgui.UIConfig.buttonSoundVolumeScale = 1;
        const waitUI = await GUIManager.loadUI(wuxian_WindowModalWaiting);
        fgui.UIConfig.globalModalWaiting = wuxian_WindowModalWaiting.URL;
        //@ts-ignore
        groot._modalWaitPane = waitUI;
    }

    protected start(): void {
        GUIManager.open(wuxian_StartGame);
        this.skyBox = director.getScene().globals.skybox;
    }

    update(dt) {
        this._dt++
        if (this._dt > skip) {
            this._dt = 0;
            this.skyBox.rotationAngle -= dt * 1;
        }
    }
}

if (DEBUG) {
    //@ts-ignore
    self["fgui"] = fgui;
    //@ts-ignore
    self["drongoCC"] = drongoCC;
    //@ts-ignore
    self["drongoGui"] = drongoGui;
}