import { Component, EventTouch, Node, Sprite, SpriteFrame, Vec3, _decorator, director, tween } from 'cc';
import { EventType, skillConfig } from '../Enum/GameEnum';
import Global, { skillBundleMgr } from '../Global';
import { BulletCtrl } from './BulletCtrl';
const { ccclass, property } = _decorator;

const btnScale = new Vec3(1.15, 1.15, 1.15);
const btnNormal = new Vec3(1, 1, 1);

const skillPos = new Vec3(0, 1.5)

@ccclass('SkillCtrl')
export class SkillCtrl extends Component {

    @property({ type: Sprite })
    cdSP: Sprite = null;

    @property({ type: Sprite })
    iconSP: Sprite = null;

    @property({ type: SpriteFrame })
    iconSFs: SpriteFrame[] = []


    private _cd = false;
    private _config: skillConfig;


    init(config: skillConfig) {
        this.node.on(Node.EventType.TOUCH_START, this._onTouchBegan, this);
        this.node.on(Node.EventType.TOUCH_END, this._onTouchDown, this);
        this._config = config;

        const sf = this.iconSFs[this._config.conf.id];

        if (sf) {
            this.iconSP.spriteFrame = sf;
        }
    }



    protected _onTouchBegan(event?: EventTouch) {

        if (!this._cd) {
            tween(this.node).to(0.15, { scale: btnScale }).start();
        }

    }
    protected _onTouchDown(event?: EventTouch) {

        if (!this._cd) {
            tween(this.node).to(0.15, { scale: btnNormal }).start();

            this.castSkill();
            const cd = this._config.conf.cd;
            const repeat = this._config.repeat + 1;
            const str = `${this._config.conf.name}  ${this._config.conf.des}  ${cd}S x${this._config.scale}  ${repeat}  `
            director.emit(EventType.SkillTip, str);

        }

    }

    async castSkill() {
        this._cd = true;
        const time = this._config.conf.time | 2;
        const name = "skill" + this._config.conf.id;
        const clip = this._config.clip;

        if (clip) {
            const clipdelay = this._config.clipdelay | 0;
            this.scheduleOnce(() => {
                skillBundleMgr.play(clip);
                const shake = this._config.shake;
                if (shake > 0) {
                    director.emit(EventType.ScreenShake, shake);
                }

            }, clipdelay)

        }

        const player = Global.playerAtk.node;

        /* just for demo,some skill's pos could be enemies' postions! */
        skillPos.set(player.position);

        director.emit(EventType.PlayAnm, "attack");

        const rad = player.eulerAngles.y * Math.PI / 180;

        const offset = this._config.conf.offset;

        if (offset != 0) {

            skillPos.x += offset * Math.sin(rad);
            skillPos.z += offset * Math.cos(rad);
        }

        const freeze = this._config.freeze;

        if (freeze > 0) {

            Global.freeze(0.2, freeze);
        }

        const eulerY = player.eulerAngles.y;

        /* you could pass player node here, if the skill is aligned to player */
        const skill = await Global.playSkill(name, true, skillPos, time, eulerY);
        skill.getComponent(BulletCtrl).init(Global.playerAtk, this._config);
        // const cd = this._config.conf.cd;
        // tween(this.cdSP).set({ fillRange: 1 }).to(cd, { fillRange: 0 }).call(() => {
        //     this._cd = false;
        // }).start();

    }






}

