@ccclass('EffectCtrl')
export class EffectCtrl extends Component {
    // [1]
    // dummy = '';

    // [2]
    @property(ParticleSystem)
    eff: ParticleSystem[] = [];
    // [2]
    @property(Animation)
    anm: Animation = null!;

    stop() {
        if (this.eff) {
            for (let i = 0; i < this.eff.length; i++) {
                this.eff[i].stop()
                this.eff[i].clear()
            }
        }
        if (this.anm) {
            this.anm.stop()

        }
    }

    play() {
        if (this.eff) {
            for (let i = 0; i < this.eff.length; i++) {
                this.eff[i].play()
            }
        }
        if (this.anm) {
            this.anm.play()

        }
    }



    clear() {

        if (this.eff) {
            for (let i = 0; i < this.eff.length; i++) {
                this.eff[i].clear()
            }
        }

    }

    // update (deltaTime: number) {
    //     // [4]
    // }
}

