import { AnimationComponent, Component, Font, LabelComponent, _decorator, } from "cc";
import { skillBundleMgr } from "../Global";

const { ccclass, property } = _decorator;



/**
 * 
 */
@ccclass("DmgAnimCtrl")
export class DmgAnimCtrl extends Component {

    @property(AnimationComponent)
    anim: AnimationComponent = null;
    @property(LabelComponent)
    text: LabelComponent = null;


    init(dmg, font: Font) {
        this.text.font = font;
        this.text.fontSize = 56 + 16 * Math.random()
        this.text.string = "" + dmg
        this.node.setPosition(-40 + 80 * Math.random(), 0)
        this.anim.play()
        this.scheduleOnce(this.recycleSelf, 0.9)

    }

    recycleSelf() {
        this.anim.stop()
        this.text.node.setPosition(0, 20)
        this.text.string = null;
        skillBundleMgr.recoverNode(this.node);
    }

}
